.fadeIn {
  animation-name: fadeIn;
  animation-fill-mode: both;
}

.fadeInLeft {
  animation-name: fadeInLeft;
  animation-fill-mode: both;
}

.fadeInUp {
  animation-name: fadeInUp;
  animation-fill-mode: both;
}

.fadeInRight {
  animation-name: fadeInRight;
  animation-fill-mode: both;
}

.fadeInDown {
  animation-name: fadeInDown;
  animation-fill-mode: both;
}

.fadeInLeft--xs {
  animation-name: fadeInLeft--xs;
  animation-fill-mode: both;
}

.fadeInDown--xs {
  animation-name: fadeInDown--xs;
  animation-fill-mode: both;
}

.fadeInUp--xs {
  animation-name: fadeInUp--xs;
  animation-fill-mode: both;
}

.fadeInRight--xs {
  animation-name: fadeInRight--xs;
  animation-fill-mode: both;
}

.jello-horizontal {
  animation-name: jello-horizontal;
  animation-fill-mode: both;
}

@media (min-width: 768px) {
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  @keyframes fadeInLeft {
    from {
      opacity: 0;
      transform: translate3d(-100%, 0, 0);
    }
    to {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translate3d(0, 100%, 0);
    }
    to {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }
  @keyframes fadeInRight {
    from {
      opacity: 0;
      transform: translate3d(100%, 0, 0);
    }
    to {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }
  @keyframes fadeInDown {
    from {
      opacity: 0;
      transform: translate3d(0, -100%, 0);
    }
    to {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }
  @keyframes jello-horizontal {
    0% {
      transform: scale3d(1, 1, 1) translateX(-50%);
    }
    30% {
      transform: scale3d(1.25, 0.75, 1) translateX(-50%);
    }
    40% {
      transform: scale3d(0.75, 1.25, 1) translateX(-50%);
    }
    50% {
      transform: scale3d(1.15, 0.85, 1) translateX(-50%);
    }
    65% {
      transform: scale3d(0.95, 1.05, 1) translateX(-50%);
    }
    75% {
      transform: scale3d(1.05, 0.95, 1) translateX(-50%);
    }
    100% {
      transform: scale3d(1, 1, 1) translateX(-50%);
    }
  }
  @keyframes fadeInUp--xs {
    0% {
      opacity: 0;
      transform: translateY(20px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  @keyframes fadeInLeft--xs {
    0% {
      opacity: 0;
      transform: translateX(-20px);
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
  @keyframes fadeInRight--xs {
    0% {
      opacity: 0;
      transform: translateX(20px);
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
  @keyframes fadeInDown--xs {
    0% {
      opacity: 0;
      transform: translateY(-20px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
}
